    border-bottom: 7px solid #717161;
    height:10px;
    width:0;
    transform-origin:left;
    animation:growproject 1s linear forwards;
    animation-delay: 1.3s;
  } */

  @keyframes growproject{
    to {width:670px;}
  }


  