/* macOs window style */
.window {
  background-color: rgba(255, 255, 255, 0.06);
  height: 150px;
  width: 600px;
  margin: auto;
  border: 1px solid #5C5C5C;
  border-top: 3px solid #5C5C5C;
  border-radius: 8px;
  backdrop-filter: blur(20px);
  margin-bottom: 50px;
  box-shadow: 1px 1px 2rem rgba(0, 0, 0, 0.3);
}

.titlebar {
  color: #4d494d;
  font-size: 11pt;
  line-height: 20px;
  text-align: center;
  width: 100%;
  height: 20px;
  border-bottom: 2px solid #b1aeb1;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  cursor: default;
}

.buttons {
  padding-left: 8px;
  padding-top: 3px;
  float: left;
  line-height: 0px;
}

.close {
  background: #ff5c5c;
  font-size: 9pt;
  width: 11px;
  height: 11px;
  border: 1px solid #e33e41;
  border-radius: 50%;
  display: inline-block;
}

.minimize {
  background: #ffbd4c;
  font-size: 9pt;
  line-height: 11px;
  margin-left: 4px;
  width: 11px;
  height: 11px;
  border: 1px solid #e09e3e;
  border-radius: 50%;
  display: inline-block;
}

.zoom {
  background: #00ca56;
  font-size: 9pt;
  line-height: 11px;
  margin-left: 6px;
  width: 11px;
  height: 11px;
  border: 1px solid #14ae46;
  border-radius: 50%;
  display: inline-block;
}

.content {
  padding: 10px;
}

.content h3 {
  margin-top: 0px;
  font-family: "poppins", sans-serif;
  font-weight: bold;
  color: #BDBDBD;
}

.window .content h2{
  font-family: "poppins", sans-serif;
  font-weight: bold;
  font-size: 18px;
  color: #BDBDBD;
}

.window .content p{
  font-family: "poppins", sans-serif;
  font-weight: bold;
  color: #BDBDBD;
}

