.about{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }
  
  .about .aboutleft .great{
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    margin-left: 50px;
    -webkit-animation-delay: 2.3s;
    -moz-animation-delay: 2.3s;
    animation-delay: 2.3s;
  }

.errorname{
  color: #BDBDBD;
}
  
.hello, .iam, .name{
    font-family: 'Monoton', cursive;
    font-size: 120px;
    font-weight: 10;
    color: #BDBDBD;
    margin-top: 0;
    margin-bottom: 0;
  }
  
  .about .dot{
    position: absolute;
    margin-top: 55px;
    margin-left: 480px;
    
   -webkit-animation-delay: 2.3s;
   -moz-animation-delay: 2.3s;
   animation-delay: 2.3s;
  }

  .about .dot h1{
    width: 50px;
    height: 50px;
    /* color: #C5C5C5; */
    color: #BDBDBD;
    font-family: 'Monoton', cursive;
    font-size: 100px;
    font-weight: 10;
  }
  
  .about .aboutright .aboutdesc h1{
    font-size: 30px;
    font-family:  'Lobster', cursive;
    color: #BDBDBD;
  }
  
  .about .aboutright .backabout{
    z-index: -9999;
    -webkit-animation-delay: 0.5s;
    -moz-animation-delay: 0.5s;
    animation-delay: 0.5s;
  }
  
  .about .backabout h1{
    font-size: 200px;
    color: #333;
    /* opacity: 0.18; */
    margin-top: 50px;
    margin-bottom: 0;
    margin-left: 100px;
  }
  
  .about .aboutright{
    margin-right: 40px;
  }

  .animate {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
  }
  
  @-webkit-keyframes fadeInLeft {
    from {
      opacity: 0;
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0);
    }
  
    to {
      opacity: 1;
      -webkit-transform: none;
      transform: none;
    }
  }
  @keyframes fadeInLeft {
    from {
      opacity: 0;
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0);
    }
  
    to {
      opacity: 1;
      -webkit-transform: none;
      transform: none;
    }
  }
  
  .fadeInLeft {
    -webkit-animation-name: fadeInLeft;
    animation-name: fadeInLeft;
  }
  
  .about .diagonalline{
    margin-top: 80px;
    margin-left: 650px;

    position: absolute;
    border-bottom: 4px solid #616161;
    height:10px;
    width:0;
    transform:rotate(70deg);
    transform-origin:left;
    animation:growabout 1s linear forwards;
    animation-delay: 1.7s;
  }
  
  @keyframes growabout{
    to {width:600px;}
  }
  
  .two {
    -webkit-animation-delay: 2.3s;
    -moz-animation-delay: 2.3s;
    animation-delay: 2.3s;
  }
  
  @-webkit-keyframes fadeInRight {
    from {
      opacity: 0;
      -webkit-transform: translate3d(100%, 0, 0);
      transform: translate3d(100%, 0, 0);
    }
  
    to {
      opacity: 1;
      -webkit-transform: none;
      transform: none;
    }
  }
  @keyframes fadeInRight {
    from {
      opacity: 0;
      -webkit-transform: translate3d(100%, 0, 0);
      transform: translate3d(100%, 0, 0);
    }
  
    to {
      opacity: 1;
      -webkit-transform: none;
      transform: none;
    }
  }
  
  .fadeInRight {
    -webkit-animation-name: fadeInRight;
    animation-name: fadeInRight;
  }

/* glitch effect */

.glitch{
  position: relative;
  animation:glitch1 1.5s 1;
  animation-delay: 4.8s;
}

@keyframes glitch1{  
  40%{
    left: -30px;
  }

  41.5%{
    left:100px;
  }

  42.5%{
    left: 0px;
  }
  
  43.5%{
    left:150px;
  }

  45%{
    left:-50px;
  }

  46.5%{
    left: 0;
  }
}

/* hint style */
.hint{
  visibility: hidden;
  margin-top: 0px;
}

.hint p{
  color: #616161;
  font-size: 15px;
  font-weight: bold;
  line-height: 0px;
}

.hint img{
  height: 50px;
  width: 50px;
  line-height: 0px;
  color: #616161;
}
  


  